@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;800&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

/* Default css */
html {
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
}

li {
  list-style: none;
}

span {
  display: block;
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  align-items: center;
  gap: 1rem;
}

input {
  font-size: 100%;
}

.spanText {
  font-size: 14px;
}

p {
  font-size: 13px;
  opacity: .7;
}

.secTitle {
  text-align: center;
  padding: 1rem 0;
  font-size: 25px;
  font-weight: 700;
  color: black;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

img {
  height: auto;
  width: 100%;
}

.icon {
  font-size: 20px;
  cursor: pointer;
}

.btn {
  border: 2px solid var(--primaryColor);
  padding: 0.5rem 1.5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  position: relative;
  color: var(--primaryColor);
  background: transparent;
  border-radius: 5px;
}

/* Media queries */
@media screen and (min-width: 960px) {
  .container {
    width: 75%;
    margin: auto;
  }

  .secTitle {
    font-size: 30px;
    padding-bottom: 2rem;
  }
}

::selection {
  background: var(--secHoverColor);
  color: white;
}

::-webkit-scrollbar {
  width: 10px;
  background: white;
}

::-webkit-scrollbar-thumb {
  background: black;
}