:root{
    --primaryColor: #3ab3a7;
    --primaryHover: #0c988a;
    --secHoverColor: #253b53;
    --greyColor: #f7f7f7;
  }

  .discount {
    width: 100%;
    padding-top: 5rem;    


    .secContainer {
      height: 400px;
      width: 100%;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      

      video {
        height: 100%;
        width: 100%;
        object-fit: cover;
        transform: scaleY(-1);
      }

      .textDiv {
        position: absolute;
        display: grid;
        padding: 2rem;
        color: white;
        text-align: center;
        gap: 1rem;

        .title {
          font-size: 18px;
          font-weight: 700;
        }
        p {
          opacity: 1;
        }

        .input_btn {
          flex-direction: column;
          gap: 1rem;

          input {
            background: #c3fff95f;
            padding: .8rem;
            border: none;
            outline: none;
            color: white;
            font-size: 13px;
            width: 95%;
            border-radius: 5px;

            &::placeholder {
              color: white;
              font-size: 12px;
            }
          }

          .btn {
            background: white;
            border: none;
            font-size: 15px;
            padding: 1rem 2rem;
            width: 95%;
            color: black;
            transition: .3s ease;

            &:hover {
              background: #1450A3;
              color: white;
            }
          }
        }
      }
    }
  }

  // MEDIA QUERIES
  @media screen and (min-width:520px) {
    .discount {
      .secContainer {
        .textDiv {
          gap: 2rem;

          .title {
            font-size: 25px;
          }

          p {
            font-size: 18px;
            max-width: 500px;
            padding: 0 3rem;
          }

          .input_btn {
            .btn {
              font-size: 16px;
            }
          }
        }
      }
    }
  }

  @media screen and (min-width:1024px) {
    .discount {
      .secContainer {
        .textDiv {
          gap: 2rem;
          .title {
            font-size: 30px;
          }
        }
      }
    }
  }