:root{
  --primaryColor: #3ab3a7;
  --primaryHover: #0c988a;
  --secHoverColor: #253b53;
  --greyColor: #f7f7f7;
}

.staff {
  background: var(--greyColor);

  .staffContainer {
      margin-top: 4rem;
      gap: 4rem;

      .singleStaff {
          position: relative;
          background: white;
          color: black;
          font-weight: 500;
          box-shadow: 0 3px 5px rgb(218, 218, 218);
          padding: 1.5rem;
          text-align: center;
          border-radius: 10px;
          transition: .4s ease;

          &:hover {
              transform: translateY(-10px);
          }

          .imgDiv {
              position: absolute;
              height: 80px;
              width: 80px;
              right: 50%;
              left: 50%;
              top: -23%;
              border-radius: 50%;
              overflow: hidden;
              transform: translateX(-50px);
              border: 10px solid white;
          }

          p {
              padding: 1rem;
              line-height: 1.3rem;
          }

          .name {
              color: var(--primaryColor);
              font-weight: 600;
              padding-top: 2rem;
              font-size: 14px;
          }

          .jobTitle {
            color: var(--secHoverColor);
            font-size: 12px;
            padding: .5rem;
            font-weight: 500;
        }

        .contact {
          color: var(--primaryColor);
          font-size: 14px;
          font-weight: 500;
        }
      }
  }
}

// MEDIA QUERIES
@media screen and (min-width: 574px) {
  .staff {
    .staffContainer {
      grid-template-columns: repeat(2,1fr);
    }
  }
}

@media screen and (min-width: 840px) {
  .staff {
    .staffContainer {
      grid-template-columns: repeat(3,1fr);
    }
  }
}